<template>
  <form 
    action=""
    class="new-todo__form"
    @submit.prevent="addNewTodo(note)" 
  >
    <input
      type="text"
      name="new-todo"
      class="input new-todo__title"
      placeholder="Enter new todo please"
      v-model.trim="note.todos.todoTitle"
    />
    <button class="btn btn--form">
      Add new todo
    </button>
    <button
      type="button"
      class="btn"
      @click="clearTodoInput"
    >
      Clear
    </button>
  </form>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import { v4 as uuidv4 } from 'uuid';

export default {
  name: 'new-todo',
  computed: {
    ...mapGetters([
      'allNotes'
    ]),     
  },
  props: {
    note: {
    type: Object,
      default: function () {
        return {
          note: {
            title: '',
            id: '',
            todos: [
              {
                id: '',
                todoTitle: '',
                completed: false,
              }
            ],
          }
        }
      },
    }
  },
  methods: {
    ...mapActions([
        'updateNotes'
      ]),
    addNewTodo(note) {
      // console.log(note);
      if (note.todos.todoTitle) {
        note.todos.push(
          {
            id: uuidv4(),
            todoTitle: note.todos.todoTitle,
            completed: false,
          }
        )
        localStorage.setItem('notes', JSON.stringify(this.allNotes));
        note.todos.todoTitle = '';
        }
        // console.log(this.$store.getters.allNotes);
    },
    clearTodoInput() {
      this.note.todos.todoTitle="";
    },
  }
}

</script>