<template>
  <div class="wrapper">
    <section class="note-vue">
      <h2 class="note-vue__title">Edit note</h2>
      <form 
        action=""
        class="note-vue__form"
        @submit.prevent="saveEditNote" 
      >
        <input
          type="text"
          name="note"
          class="input note__title"
          v-model.trim="note.title"
        />
        <button class="btn btn--form">
          Save changes
        </button>
        <button
          type="button"
          class="btn"
          @click="cancel"
        >
          Cancel
        </button>
      </form>
      <NewTodo :note="currentNote"/>
      <CurrentTodos 
        :currentNote="currentNote"
        @finishEditing="finishEditing(currentTodo = $event)"
        />
    </section>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex';
  import NewTodo from '@/components/NewTodo';
  import CurrentTodos from '@/components/CurrentTodos';

  export default {
    name: 'main-layout',
    components: {
      NewTodo,
      CurrentTodos,
    },
    data() {
      return {
        note: {
          title: this.currentNote.title,
          id: this.currentNote.id,
          todos: this.currentNote.todos,
        },
        currentTodo: '',
      }
    },
    computed: {
      ...mapGetters([
        'allNotes',
        'currentNoteId'
      ]),
    },
    props: {
      currentNote: {
      type: Object,
        default: function () {
          return {
            note: {
              title: '',
              id: '',
              todos: [],
            }
          }
        },
      }
    },
    methods: {
      ...mapActions([
        'updateNotes',
        'setCurrentNoteId'
      ]),
      cancel() {
        this.$router.push({ name: 'home' })
      },
      finishEditing(todo) {
        if (todo.todoTitle) {
          const updatedTodos = this.note.todos.map(task => {
            if (todo.id === task.id) {
              return {
                ...task,
                id: task.id,
                todoTitle: task.todoTitle,
                completed: task.completed,
              }
            }
            return task;
          });
          this.note.todos = updatedTodos;
        }
        localStorage.setItem('notes', JSON.stringify(this.allNotes));
      },
      saveEditNote () {
        if (!this.note.title) {
          return
        }
        const newNote = {
          id: this.note.id,
          title: this.note.title,
          todos: this.note.todos
        }
        this.$store.dispatch('saveEditNote', newNote);
        localStorage.setItem('notes', JSON.stringify(this.$store.getters.allNotes))
        this.$router.push({ name: 'home' })
      }
    },
  }
</script>

<style lang="scss">
  .wrapper {
    max-width: 1080px;
    margin: 0 auto;
  }

  .note-vue {
    display: inline;
    background-color: rgba(9,30,66,.04);
  }
</style>