<template>
  <div>
    <ul class="todo-list">
      <li
        v-for="todo in note.todos" :key="todo.id"
        class="todo-list__item"
        :class="{ completed: todo.completed }"
      >
        <div class="view">
          <input class="toggle" type="checkbox" v-model="todo.completed" />
          <label  v-if="editTodoId !== todo.id">{{ todo.todoTitle }}</label>
          <input 
            v-else class="input todo-item__edit" 
            type="text" 
            v-model="todo.todoTitle"
            @blur="finishEditing" 
            @keyup.enter="finishEditing(todo)"
          >
        </div>
        <div>
          <button class="btn todo-list__btn" @click="editTodo(todo)">Edit</button>
          <button class="btn todo-list__btn" @click="deleteTodo(todo.id)">Delete</button>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'current-todos',
  data() {
    return {
      note: {
        title: this.currentNote.title,
        id: this.currentNote.id,
        todos: this.currentNote.todos,
      },
      editTodoId: '',
    }
  },
  computed: {
    ...mapGetters([
      'allNotes',
    ]),
  },
  props: {
    currentNote: {
    type: Object,
      default: function () {
        return {
          note: {
            title: '',
            id: '',
            todos: [
              { 
                id: '',
                todoTitile: '',
                completed: false,
              }
            ],
          }
        }
      },
    }
  },
  methods: {
    ...mapActions([
      'updateNotes'
    ]),
    deleteTodo(id) {
      let updatedTodos = this.note.todos.filter(todo => {
        // console.log(todo.id);
        return id !== todo.id
      });
      console.log(updatedTodos, this.allNotes);
      this.note.todos = updatedTodos;
      // console.log(this.$store.getters.allNotes);
      // this.updateNotes(this.allNotes.filter(note => currentNote.id !== note.id));
    },
    editTodo(todo) {
      // console.log(todo);
      this.$emit('editTodo', todo)
      this.editTodoId = todo.id
    },
    finishEditing(todo) {
      // console.log(todo);
      this.$emit('finishEditing', todo);
      this.editTodoId = ''
      // this.editTodoId = ''
      // localStorage.setItem('notes', JSON.stringify(this.allNotes));
      // console.log(this.$store.getters.allNotes);
    }
  },
}
</script>

<style lang="scss">
  .todo-list {
    width: 100%;
    border-radius: 0.4rem;
    background-color: #EBECF0;
    padding: 1rem;
    /* background-color: $color-primary; */

    &__item {
      display: flex;
      justify-content: space-between;
      background-color: #fff;
      margin: 1rem;
      padding: 1rem;
    }

    &__btn {
      margin-right: 1rem;
      border-color: $color-primary-dark;
    }

    .completed {
      text-decoration: line-through;
      color: grey;
    }
  }
</style>